import React from 'react';
import WebRTCAdaptor from '../js/webrtcAdaptor';
import RoomState, { roomOfStream } from '../interface';
import WebinarTools from '../services/webinarTools';
import io from 'socket.io-client';
import {connect} from 'react-redux';
import UserRoom from '../components/userRoom';
import AdminRoom from '../components/adminRoom';
import { Redirect } from 'react-router-dom';

interface IProps {
    rooms?: string;
    match?:any;
}


class Room extends React.Component<any,RoomState>{
    constructor(props:IProps) {
        super(props)
        this.state = {
            streamId:null,
            token:null ,
            playOnly:false ,
            roomOfStream :new Array(),
            publishStreamId:null,
            isMicMute:false,
            ifCamaraOff:false ,
            showVideo:false ,
            websocketURL:"wss://" + 'videoconference.ulive.io' + ":5443" + '/LiveApp/' + "websocket",
            adapter:null ,
            webRTCAdaptor:null,
            roomNameBox:'room1',
            userName:'',
            camaraId:'',
            socket:null,
            isDataChanelOpen:false ,
            isAdmin:false,
            waitCreateRoom:false,
            redirect:false,
            socketId:null
        }
        
    }


    async componentDidMount() {
        const { match: { params },setRoomName } = this.props;
        const room = params.roomId; 
        
        let valid = false;
        this.setState({...this.state,roomNameBox:room});
        setRoomName(room);

        await fetch(`https://webinar-zentv-front.herokuapp.com//webinar/validRoom`,
        { method: 'POST',

        mode: 'cors',
        headers: new Headers({
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        }),
        cache: 'no-cache',
        body:JSON.stringify({
            'user':'Admin',
            'roomId':room,
            'adminRoom':{
                'isAdmin':true,
                'streamId':null,
            }
        }),
        })
        .then(res => res.json()).then((res)=>{
            valid = res.valid
         });

        this.createRoom(valid);

    }

    createRoom = async(valid:boolean)=>{
        if (valid) {
            await fetch(`https://videoconference.ulive.io:5443/LiveApp/rest/v2/broadcasts/create`,
                { 
                    method: 'POST',
                    headers:{
                            'Content-Type': 'application/json'
                            },
                    mode: 'cors',
                    cache: 'default' 
                }).then(res => res.json()).then((res)=>{
                    this.setState({...this.state,
                    streamId:res.streamId,
                });
            });
                
                await this.webRTCInit();
    
        }else{
           this.setRedirect();
        }
    }


    webRTCInit = async  ()=>{
        let thiz = this
        const{websocketURL}=this.state;
        let pc_config =
          {
            'iceServers' : [ {
              'urls' : 'stun:stun.l.google.com:19302'
            } ]
          };
        let sdpConstraints = {
            OfferToReceiveAudio : true,
            OfferToReceiveVideo : true
              
        };
        let mediaConstraints = {
            video: true,
            audio: true
        };
        let webRTCAdaptor = new WebRTCAdaptor({
            websocket_url : websocketURL,
            mediaConstraints : mediaConstraints,
            peerconnection_config : pc_config,
            sdp_constraints : sdpConstraints,
            localVideoId : "localVideo",
            debug : true,
            callback : function(info:any, obj:any) {
                if (info ==="pong") {
                    return
                }
                if (info == "initialized") {
                    thiz.joinRoom();
                } else if (info == "joinedTheRoom") {	
                if(thiz.state.playOnly) {
                  console.log('playOnly')
                }
                else {
                  thiz.state.webRTCAdaptor.publish(obj.streamId);
                }
                if (obj.streams != null) {
                    console.log(obj.streams)
                  obj.streams.forEach((item:any)=> {
                    thiz.state.webRTCAdaptor.play(item, thiz.state.token,thiz.state.roomNameBox);
                  });
                }                        
                } else if (info == "streamJoined") {
                    const {playOnly,token,roomNameBox}=thiz.state;
                    thiz.state.webRTCAdaptor.play(obj.streamId, token,roomNameBox)             
                } else if (info == "newStreamAvailable") {
                    thiz.playVideo(obj);       
                } else if (info == "streamLeaved") {
                    thiz.removeRemoteVideo(obj.streamId);			
                } else if (info == "publish_started") {
                    thiz.props.setAdmin(true);
                    thiz.setState({...thiz.state,isAdmin:true});
                    thiz.setAdmin(obj);					
                }else if (info == "publish_finished") {
                //called when stream publishing has finished for this client				
                }else if (info == "leavedFromRoom") {
                            //called when this client is leaved from the room  
                }else if (info == "closed") {
                    if (typeof obj != "undefined") {
                        console.log("Connecton closed: "+ JSON.stringify(obj));
                    }	
                    //Llamar al endpoitn que borrar el streamId		
                } 
                else if (info == "play_finished") {
                    let video:any = document.getElementById("remoteVideo"+ obj.streamId);
                    if (video != null) {
                        video.srcObject = null;
                    }
                //called when a stream has finished playing			
                } else if (info == "streamInformation") {
                    thiz.state.webRTCAdaptor.streamInformation(obj);
                } else if (info == "data_channel_opened") {
                    thiz.setState({...thiz.state,isDataChanelOpen:true});
                } else if (info == "data_channel_closed") {
                    thiz.setState({...thiz.state,isDataChanelOpen:false});
                } else if(info == "data_received") {
                    thiz.state.webRTCAdaptor.handleNotificationEvent(obj);
                }
                },
                callbackError : function(error:any, message:any) {
                //some of the possible errors, NotFoundError, SecurityError,PermissionDeniedError
                }},true);

            this.setState({...this.state,webRTCAdaptor:webRTCAdaptor});
    }

    setAdmin = (obj:any)=>{
        // let admin = {
        //     isAdmin:true,
        //     streamId:obj.streamId,
        //     stream:obj.stream,
        //     socketId:string;
        // }
    }

    joinRoom = async()=> {
        const{roomNameBox,streamId}=this.state;
        await this.initSocket();
        await this.state.webRTCAdaptor.joinRoom(roomNameBox, streamId);
    }
    
    leaveRoom = (stream:any) =>{
        const { setRoomOfStream } = this.props;
        const{webRTCAdaptor,roomNameBox,roomOfStream}=this.state;
        this.state.webRTCAdaptor.leaveFromRoom(roomNameBox);
        this.state.webRTCAdaptor.muteLocalMic(false);
        this.state.webRTCAdaptor.turnOffLocalCamera(false);
        const auxRooms = roomOfStream.filter(element => element.streamId !== stream.streamId);
        setRoomOfStream(roomOfStream);
        this.setState({...this.state,roomOfStream:auxRooms,showVideo:false});
    }

    playVideo = (obj:any) => {
        const { setRoomOfStream } = this.props;
        const {socketId}=this.state;
        debugger
        if (obj.streamId) {
            let video:roomOfStream = {
            streamId:obj.streamId,
            stream:obj.stream,
            videoRef : React.createRef(),
            ifCamaraOff:true,
            microMute:true,
            socketId:socketId
        };
        let clients = this.state.roomOfStream;
        if (clients.length > 0 ) {
            const valid = clients.some(e => e.streamId === video.streamId);
            if (!valid) {
                clients.push(video)
            }   
        }else{
            clients.push(video);
        }
            setRoomOfStream(clients);
            this.setState({...this.state,roomOfStream:clients,showVideo:true});
        }
    }

    removeRemoteVideo = async(streamId:any) => {
        await fetch('https://videoconference.ulive.io:5443/LiveApp/rest/v2/broadcasts/'+streamId,
            {
                method:'DELETE',
                headers:{
                    'Content-Type': 'application/json'
                }, 
                mode: 'cors',
                cache: 'default'
            }
        ).then((res =>res.json()));
    }

    handleChange = (e:any)=>{
        const { id , value} = e.target;
        this.setState({...this.state,[`${id}`]:value});
    }

    initSocket = ()=>{

        const {streamId,roomNameBox,userName}=this.state
        const {setSocket}=this.props;
        const socket = io('http://localhost:5000');
        setSocket(socket);
        this.setState({...this.state,socket},()=>{
            socket.on('connect', (sockets:any)=>{  
                socket.emit('entrarChat',
                { 
                    streamId,
                    roomNameBox,
                    userName
                });
            });
        });

        socket.on('entrarChat',(data:any)=>{
            this.setState({...this.state,socketId:data.socketId})
        });

        socket.on('turnOffCamara',(data:any)=>{
            if (data.turnOffLocalCamera) {
            this.turnOffLocalCamera(true);
            }
        });

        socket.on('muteMicro',(data:any)=>{
            if (data.turnOffLocalCamera) {
            this.muteLocalMic(true);
            }
        });
        this.setState({...this.state,waitCreateRoom:true})
    }
        
    emitLeaveRoom = async()=>{
        const{socket,streamId,roomOfStream}=this.state;
        socket.on('leaveRoom',{
            streamId:streamId
        },(data:any)=>{
            if (data.leaveRoom) {
            const newRoomOfStream = roomOfStream.filter((e)=>e.streamId !== streamId);
            this.setState({...this.state,roomOfStream:newRoomOfStream});
            }
        });
    }

    turnOffLocalCamera = (isCameraOff:boolean)=> {
        if (isCameraOff) {
            this.state.webRTCAdaptor.turnOffLocalCamera();
            this.sendNotificationEvent("CAM_TURNED_OFF");
        }else{
            this.state.webRTCAdaptor.turnOnLocalCamera();
            this.sendNotificationEvent("CAM_TURNED_ON");
        }
    }

    muteLocalMic = (isMicMuted:boolean)=>{
        if (isMicMuted) {
            this.state.webRTCAdaptor.muteLocalMic();
            this.sendNotificationEvent("MIC_MUTED");
        }else{
            this.state.webRTCAdaptor.unmuteLocalMic();
        }
    }

    sendNotificationEvent =(eventType:any)=> {
      const {isDataChanelOpen,publishStreamId,webRTCAdaptor}=this.state;
          if(isDataChanelOpen) {
              let notEvent = { streamId: publishStreamId, eventType:eventType };
              webRTCAdaptor.sendData(publishStreamId, JSON.stringify(notEvent));
          }	else {
              console.log("Could not send the notification because data channel is not open.");
        }
    }

    setRedirect = () => {
        this.setState({
          redirect: true
        })
    }
    renderRedirect = () => {
        if (this.state.redirect) {
            return <Redirect to={`/`}/>
        }
    }

    render(){
        const {webinarReducer}= this.props
        return(
        <div className='w100'>
            <div id="players" >
                <div className="col">
                    <video id="localVideo" autoPlay muted  width="200" height="200"/>
                </div>
            </div>
            {webinarReducer.isAdmin ? 
            <AdminRoom /> : 
            <UserRoom 
                camaraOff={()=>{this.turnOffLocalCamera(true)}}
                microMute={()=>{this.muteLocalMic(true)}}
            />}
            {this.renderRedirect()}
        </div>
        )
    }
}

const mapStateToProps = (state:any)=>{
    return{
        webinarReducer: state.webinarReducer
    }
}
const mapDispatchToProps = (dispatch:any) => {
    return {
        setRoomOfStream:(rooms:any) =>{
            dispatch({ type: "set_rooms", payload: rooms })
        },
        setRoomName:(rooms:any) =>{
            dispatch({ type: "set_room_name", payload: rooms })
        },
        setSocket:(socket:any) =>{
            dispatch({ type: "set_socket", payload: socket })
        },
        setAdmin:(admin:boolean)=>{
            dispatch({ type: "set_admin", payload: admin })
        }
    }
}
export default connect(mapStateToProps,mapDispatchToProps) (Room);