import React from 'react';
import RoomState from '../interface';
import VideoStream from './video';
import { connect } from 'react-redux';
import GridLayout from 'react-grid-layout';
import { WidthProvider, Responsive } from "react-grid-layout";
import 'react-grid-layout/css/styles.css' 
import 'react-resizable/css/styles.css'
import ReactGridLayout from 'react-grid-layout';
import {ShareScreenIcon,MicOnIcon,MicOffIcon,CamOnIcon,CamOffIcon} from '../js/Icons';


class AdminRoom  extends React.Component<any,any> {
    constructor(props:any) {
        super(props);
        this.state = {
            col:{ lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 },
        }
    }


    generateDOM= ()=> {
        return this.props.webinarReducer.roomOfStream.map((v:any,index:any)=>{
            return (
                <div key={`${index}`} style={{zIndex:1000}}>
                    <VideoStream stream={v.stream}/>
                </div>
              );
        })
    }

    emitCamaraOff = (streamId:any,turnOffCamara:boolean)=>{
        const{webinarReducer}=this.props;
        webinarReducer.socket.emit('turnOffCamara',{
            streamId,
            turnOffCamara
        });
    }

    emitMuteOff = (streamId:any,muteMicro:boolean) =>{ 
        const{webinarReducer}=this.props;
        webinarReducer.socket.emit('muteMicro',{
            streamId,
            muteMicro
        });
    }


    render(){
        const {webinarReducer}=this.props;

        const layout = [
            {i: '0', x: 0, y: 0, w: 2, h: 2, maxW:Infinity,isResizable:true,isDraggable:true },
            {i: '1', x: 5, y: 0, w: 2, h: 2,  maxW:Infinity},
            {i: '2', x: 10, y: 10, w: 1, h: 2 , maxW:Infinity}
        ];
  
          return(
              <div className=" w100">
                <ReactGridLayout  
                className="layout" 
                cols={3}
                width={1200}
                layout={layout}
                rowHeight= {300}
                >
                
              {
                  this.props.webinarReducer.roomOfStream.map((v:any,index:any)=>{
                    return (
                        <div key={`${index}`} style={{zIndex:1000}}>
                            <VideoStream stream={v.stream}/>
                            <div className="w100" style={{zIndex:1001,position:'absolute',bottom:0}}> 
                            <button className='control-btn'>
                                <ShareScreenIcon />
                            </button>
                            <button className='control-btn' onClick={()=>{this.emitMuteOff(v.streamId,true)}}>
                            {
                               !webinarReducer.isMicMute?(
                                <MicOnIcon/>
                                ):(
                                <MicOffIcon/>
                                )
                            }
                            </button>
                            <button className='control-btn' onClick={()=>{this.emitCamaraOff(v.streamId,true)}}>
                                {
                                     !webinarReducer.ifCamaraOff?(
                                    <CamOnIcon/>
                                    ):(
                                    <CamOffIcon/>
                                    )
                                }
                            </button>
                            </div>
                        </div>
                      );
                })
              }
                </ReactGridLayout >
              {webinarReducer.showVideo && (
                <div className='status'>
                    <p>Establishing connection...</p>
                </div>
              )}
          </div>
          )
      }
}

const mapStateToProps = (state:any)=>{
    return{
        webinarReducer: state.webinarReducer
    }
}


export default connect(mapStateToProps) (AdminRoom);


