import React from 'react'
import {Redirect}  from 'react-router-dom';
import { connect } from 'react-redux';

interface IProps {
    rooms?: string;
}
interface IState {
    roomName: string;
    redirect:boolean;
}

class GoToRoomInput extends React.Component<any,IState> {

    constructor(props:IProps) {
        super(props)
        this.state = {
            roomName:'room1',
            redirect:false,
        }
    }

    componentDidMount = async()=>{
        await fetch('https://webinar-zentv-front.herokuapp.com/webinar/createHash',
        { 
            method: 'GET',
            headers:{'Content-Type': 'application/json'},
            mode: 'cors',
            cache: 'default' 
        }).then(res => res.json()).then((res => {
            this.setState({...this.state,roomName:res.roomsHash});
        }))
    
    }
    handleChange = (e:any)=>{
        const { id , value} = e.target;
        this.setState({...this.state,[`${id}`]:value});
    }
    setRedirect = () => {
        this.setState({
          redirect: true
        })
    }
    renderRedirect = () => {
        const {setAdmin}=this.props;
        if (this.state.redirect) {
            setAdmin(true);
            return <Redirect to={`/${this.state.roomName}`}/>
        }
    }
    handleChangeRedirect = ()=>{
        this.createRoom()
    }

    createRoom = async()=>{
        const {roomName}=this.state;

       await fetch(`https://webinar-zentv-front.herokuapp.com//webinar/createRoom`,
       { method: 'POST',
       headers:{
         'Content-Type': 'application/json'
       },
        mode: 'cors',
       body:JSON.stringify({
            'user':'Admin',
            'roomId':roomName,
            'adminRoom':{
                'isAdmin':true,
                'streamId':null,
            }
        }),
       })
       .then(res => res.json()).then((res)=>{
            this.setState({...this.state,redirect:true});
       }).
       catch((error)=> {
           console.log('Hubo un problema con la peticiÃ³n Fetch:' + error.message);
       });    
   }

    render() {
        return(
            <div className="enter-room-container">
                    <input type="text" value={this.state.roomName} id={this.state.roomName} onChange={(e)=>{this.handleChange(e)}}/>
                    <button onClick={()=>{
                        this.handleChangeRedirect();
                    }}>Enter</button>
                {this.renderRedirect()}
            </div>
        )
    }
}
const mapDispatchToProps = (dispatch:any) => {
    return {
        setAdmin:(admin:boolean)=>{
            dispatch({ type: "set_admin", payload: admin })
        }
    }
}
export default connect(null,mapDispatchToProps) (GoToRoomInput);