import React from 'react'
import VideoStream from './video';
import { connect } from 'react-redux';
import GridLayout from 'react-grid-layout';
import ReactGridLayout from 'react-grid-layout';
import { ShareScreenIcon, MicOnIcon, CamOffIcon } from '../js/Icons';


class UserRoom extends React.Component<any,any> {
    constructor(props:any) {
        super(props);
        this.state={
          isMicMute:false,
          ifCamaraOff:false
        }
    }

    render(){

      const layout = [
        {i: 'a', x: 0, y: 0, w: 2, h: 2, static: true},
        {i: 'b', x: 2, y: 0, w: 2, h: 2, minW: 2, maxW: 4},
        {i: 'c', x: 4, y: 0, w: 1, h: 2}
      ];

        const {webinarReducer}=this.props;
        return(
        <div className=" w100">
          <ReactGridLayout  
          className="layout" 
          cols={3}
          width={1200}
          layout={layout}
          rowHeight= {300}
          >
          
        {
            this.props.webinarReducer.roomOfStream.map((v:any,index:any)=>{
              return (
                  <div key={`${index}`} style={{zIndex:1000}}>
                      <VideoStream stream={v.stream}/>
                  </div>
                );
          })
        }
        </ReactGridLayout >
        <div className="w100" style={{zIndex:1001,position:'absolute',bottom:0}}> 
          <button className='control-btn'>
              <ShareScreenIcon />
          </button>
          <button className='control-btn' onClick={this.props.microMute}>
            {
                !webinarReducer.isMicMute?(
                <MicOnIcon/>
                ):(
                <MicOnIcon/>
                )
            }
          </button>
          <button className='control-btn' onClick={this.props.camaraOff}>
              {
                    !webinarReducer.ifCamaraOff?(
                  <CamOffIcon/>
                  ):(
                  <CamOffIcon/>
                  )
              }
          </button>
        </div>
        {webinarReducer.showVideo && (
          <div className='status'>
              <p>Establishing connection...</p>
          </div>
        )}
    </div>
        )
    }
}
const mapStateToProps = (state:any) => {
    return{
        webinarReducer: state.webinarReducer
    }
}
const mapDispatchToProps = (dispatch:any) => {
  return {
      setAdmin:(admin:boolean)=>{
          dispatch({ type: "set_admin", payload: admin })
      },
      setCamara:(camara:boolean)=>{
        dispatch({ type: " camara_off", payload: camara })
      },
      setMicro:(micro:boolean)=>{
        dispatch({ type: "micro_mute", payload: micro })
      }
  }
}

export default connect(mapStateToProps,mapDispatchToProps) (UserRoom);