import RoomState from '../interface';
import store from '../../redux/store';

const initState:RoomState = {
    streamId:null,
    token:null ,
    playOnly:false ,
    roomOfStream :new Array(),
    publishStreamId:null,
    isMicMute:false,
    ifCamaraOff:false ,
    showVideo:false ,
    websocketURL:"wss://" + 'videoconference.ulive.io' + ":5443" + '/LiveApp/' + "websocket",
    adapter:null ,
    webRTCAdaptor:null,
    roomNameBox:'room1',
    userName:'',
    camaraId:'',
    socket:null,
    isDataChanelOpen:false ,
    isAdmin:false,
    socketId:null,
}

const webinarReducer = (state = initState,actions:any)=>{
    switch (actions.type) {
        case'camara_off':{
            return{
                ...state,
                ifCamaraOff:actions.payload
            }
        }
        case'micro_mute':{
            return{
                ...state,
                isMicMute:actions.payload
            }
        }
        case'leave_room':{
            return{
                ...state,
                roomNameBox:actions.payload
            }
        }
        case'admin_room':{
            return{
                ...state,
                isAdmin:actions.payload
            }
        }
        case'add_member_room':{
            return{
                ...state,
                roomOfStream:actions.payload
            }
        }
        case'set_rooms':{
            return{
                ...state,
                roomOfStream:actions.payload
            }
        }
        case'set_room_name':{
            return{
                ...state,
                roomNameBox:actions.payload
            }
        }
        case 'set_socket':{
            return{
                ...state,
                socket:actions.payload
            }
        }
        case 'set_admin':{
            return{
                ...state,
                isAdmin:actions.payload
            }
        }
        case 'set_sockets_id':{
            return{
                ...state,
                socketId:actions.payload
            }
        }

        default:
            return state;
    }
}

export default webinarReducer;